import Vue from 'vue'
import Router from 'vue-router'

import seconds from '@/components/seconds'
import three from '@/components/three'
import four from '@/components/four'
Vue.use(Router)

export default new Router({
routes: [
	{
      path: '/',
      name: 'seconds',
      component: seconds
   },
    {
      path: '/seconds',
      name: 'seconds',
      component: seconds
    },
    {
      path: '/three',
      name: 'three',
      component: three
    },
    {
      path: '/four',
      name: 'four',
      component: four
    }
]
})



