<template>
  <div class="seconds">
     <div class="left">
     	<ul>
     		<li v-for="val,index in list" @click="inf(index)">{{val.tit}}</li>
     	</ul>
     </div>
     <div class="right">
     		<dl v-for="val,index in childlist">
     			<dd>
     				<img :src="val.img" />
     			</dd>
     			<dt>{{ val.name}}</dt>
     			<p>{{val.datatype}}</p>
     		</dl>
     	
     </div>
  </div>
</template>

<script>
	import axios from "axios"
export default {
  name: 'seconds',
  data:function(){
  	return {
  		list:[],
  		childlist:[]
  	}
  },
  created:function(){
  	var that=this
  	axios.get("../../static/js/list.json").then(function(res){
  		
  			that.list=res.data.type
  				
  	})
  	
  	
  	
  },
  methods:{
  	inf(index){
  		this.childlist=this.list[index].datainfo
  		console.log(this.childlist)
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



